<template>
	<div id="statcenter-takeout">
		<public-header title="外卖统计"></public-header>
		<div class="content">
			<div class="padding-15">
				<span class="font-16">{{store.title ? store.title : '全部店铺'}}</span>
				<span class="c-gray font-14">{{filter.items && filter.items.stat_day ? filter.items.stat_day.start + '-' + filter.items.stat_day.end : '今天'}}</span>
			</div>
			<van-cell-group>
				<van-cell title="营业总额" :value="'¥' + stat.total_fee" />
				<van-cell title="总入账" :value="'¥' + stat.total_final_fee" />
				<van-cell title="有效订单量" :value="'¥' + stat.total_success_order" />
				<van-cell title="客单价" :value="'¥' + stat.avg_pre_order" />
				<van-cell title="无效订单量" :value="'¥' + stat.total_cancel_order" />
				<van-cell title="损失营业额" :value="'¥' + stat.total_cancel_fee" />
			</van-cell-group>
			<van-cell-group class="margin-10-t">
				<van-cell title="佣金收入" :value="'¥' + stat.total_serve_fee" />
				<van-cell title="配送费收入" :value="'¥' + stat.total_delivery_fee" />
				<van-cell title="配送员配送费支出" :value="'¥' + stat.total_deliveryer_fee" />
				<van-cell title="平台补贴" :value="'¥' + stat.plateform_discount_fee" />
				<van-cell title="附加费" :value="'¥' + stat.total_extra_fee" />
				<van-cell title="总退款" :value="'¥' + stat.total_refund_fee" />
			</van-cell-group>
			<div class="padding-10">
				<van-button type="primary" size="normal" block class="font-16" @click="util.jsUrl('pages/statcenter/current')">查看详情</van-button>
			</div>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import {mapState, mapMutations} from 'vuex'
	import publicHeader from "@/components/header"
	import {getData} from "../../controller/funcCommon.js"
	export default {
		components: {
			publicHeader,
		},
		data() {
			return {
				showPreLoading: true,
				stat: {},
				store: {}
			}
		},
		methods: {
			...mapMutations([
				'setSearch'
			]),

			onLoad() {
				let that = this;
				getData({
					vue: that,
					data: {
						filter: that.filter ? that.filter.items: {}
					},
					url: 'plateform/statcenter/takeout/takeout',
					success: function(result) {
						that.stat = result.stat;
						that.store = result.store;
					}
				});
			}
		},
		computed: {
			...mapState([
				'filter',
			]),
		},
		mounted() {
			this.onLoad();
		},
	}
</script>

<style>
	#statcenter-takeout .van-cell .van-cell__value span{
		font-weight: 600;
		font-size: 16px;
	}
</style>


